@import url(https://fonts.googleapis.com/css2?family=Oxygen&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    /* overflow: hidden; */
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    font-family: 'Oxygen', sans-serif;
    background-color: rgb(87, 139, 182);
    /* height: 100%; */
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;

}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-thumb {
    background-color: rgb(160, 159, 165);
}

.main_div {
    position: relative;
    width: 100vw;
    height: 100vh;
}


.center_div {
    color: rgb(9, 129, 199);
    /* background-color: #ffffff; */
    transition: all 0.2s linear;
    word-break: break-all;
    overflow-y: scroll;
    padding: 33px;
    height: 100%;
    font-size: 25px;
}

input {
    outline: none;
    width: 80%;
    height: 30px;
    font-weight: 300;
    padding: 5px;
    border-radius: 0;
    border: none;
    font-size: 1em;
}

h1 {
    transition: all 0.5s;

    color: rgb(0, 0, 0);
    text-shadow: 3px 3px 3px rgba(7, 115, 134, 0.808);
}

#inp {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    border: 1px solid rgba(7, 96, 240, 0.507);
}

button {
    cursor: pointer;
    border: none;
    border-radius: 0;
    width: 20%;
    height: 30px;
    background-color: rgb(13, 151, 206);
    color: white;
}

button:active {
    background-color: rgb(16, 8, 129);
}

.todo_style {
    margin: 5px;
    display: -webkit-flex;
    display: flex;
}

.cross {

    cursor: pointer;
    font-size: 0.8em;
    color: rgb(255, 255, 255);
    border-radius: 50%;
    height: 30px;
    width: 30px;
    overflow: hidden;
    text-align: center;
    background-color: rgb(1, 79, 131);
    margin-right: 15px;
}
.switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 24px;
    margin-left: 75%;
    
  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left:0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
